

<script>
import { mapState } from "vuex";

const BASE_STYLE = { position: "absolute" };

export default {
   props: {
      show: {
         default: false
      },
      work: {
         type: Object,
         default: () => {}
      }
   },
   computed: {
      ...mapState({
         page: state => state.page.pageInfo
      })
   },
   watch: {
      show: {
         handler(val) {
            if (val) {
               /**
                * 修改背景
                */
               this.pageStyle = this.shallowCopy(this.page).style;
               this.num2px(
                  Object.assign(this.pageStyle, {
                     position: "relative",
                     overflow: "hidden"
                  })
               );

               /**
                * 修改插件
                */
               this.pluginsSytle = this.shallowCopy(this.page.plugins);

               this.pluginsSytle.map(it => {
                  this.num2px(Object.assign(it.wrapStyle, BASE_STYLE));
                  this.num2px(Object.assign(it.nodeStyle, BASE_STYLE));
               });
            }
         },
         deep: true,
         immediate: true
      }
   },
   mounted() {
      console.log("挂在完成");
   },
   updated() {
      this.$nextTick(() => {
         console.log("更新完成");
      });
   },
   methods: {
      renderPreview(h) {
         if (!this.show) return <div></div>;
         return (
            <div class="grg-page" vShow={this.show}>
               <div style={this.pageStyle} class="page-bg">
                  {this.pluginsSytle.map(ele => {
                     return (
                        <div class="node" style={ele.wrapStyle}>
                           {<ele.name style={ele.nodeStyle}></ele.name>}
                        </div>
                     );
                  })}
               </div>
            </div>
         );
      },

      shallowCopy(val) {
         return JSON.parse(JSON.stringify(val));
      },
      num2px(obj) {
         if (Object.prototype.toString.call(obj) !== "[object Object]") return;
         Object.keys(obj).forEach(pro => {
            if (
               pro == "width" ||
               pro == "height" ||
               pro == "top" ||
               pro == "left" ||
               pro == "border-width"
            ) {
               obj[pro] = obj[pro] + "px";
            } else if (pro == "transform") {
               obj[pro] = "rotate(" + obj[pro] + "deg)";
            } else if (pro == "border-radius") {
               obj[pro] = obj[pro] + "%";
            }
         });
      }
   },
   render(h) {
      return this.renderPreview(h);
   }
   //    methods: {
   //       shallowCopy(val) {
   //          return JSON.parse(JSON.stringify(val));
   //       },
   //       num2px(obj) {
   //          if (Object.prototype.toString.call(obj) !== "[object Object]") return;
   //          Object.keys(obj).forEach(pro => {
   //             if (
   //                pro == "width" ||
   //                pro == "height" ||
   //                pro == "top" ||
   //                pro == "left" ||
   //                pro == "border-width"
   //             ) {
   //                obj[pro] = obj[pro] + "px";
   //             } else if (pro == "transform") {
   //                obj[pro] = "rotate(" + obj[pro] + "deg)";
   //             } else if (pro == "border-radius") {
   //                obj[pro] = obj[pro] + "%";
   //             }
   //          });
   //       }
   //    }
};
</script>

